.tech-widget {
  @extend %list;
  position: absolute;
  right: 0;
  padding: 1vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1vw;
  color: $accent;
}

.tech-widget__item {
  width: 2vw;
  height: 2vw;
}

.tech-widget__icon {
  width: 100%;
  height: 100%;
}
